/* COURCES */

.courceContainer {
  display: flex;
  padding: 50px 100px;
  justify-content: space-between;
  align-items: center;
  background-color: #f2f5f5;
}
.sliderCardContainer div {
  margin-bottom: 10px;
}
.sliderCardContainer {
  width: 150px;
  background-color: #fff;
  padding: 20px;
}
.courcecontainer div:first-child {
  padding: 20px;
}

/* MEDIA QURIES */

@media (max-width: 600px) {
  .courceContainer {
    padding: 50px 5px;
  }
  .courceContainer div:first-child h1 {
    font-size: 22px;
  }
}

@media (max-width: 1023px) {
  .courceContainer {
    flex-direction: column;
  }
}
