/* COURCES SLIDER CSS */

.embla {
  --slide-spacing: 1rem;
  --slide-size: 27%;
  --slide-height: 12rem;
  padding: 1.6rem;
  width: 750px;
}
.emblaViewport {
  overflow: hidden;
}
.emblaContainer {
  display: flex;
  touch-action: pan-y;
}

.emblaSlide {
  flex: 0 0 var(--slide-size);
  min-width: 0;
  margin-left: var(--slide-spacing);
  padding: var(--slide-spacing);
  position: relative;
  background-color: #fff;
  border: none;
  border-radius: 20px;
}

.emblaSlideImg {
  display: block;
  height: var(--slide-height);
  width: 100%;
  object-fit: cover;
}
.emblaButtons {
  display: flex;
  justify-content: end;
  margin: 10px 5% 0px 0px;
}
@media (max-width: 800px) {
  .embla {
    --slide-size: 43%;
    width: 550px;
  }
}
@media (max-width: 600px) {
  .embla {
    --slide-size: 90%;
    width: 350px;
  }
}
@media (max-width: 400px) {
  .embla {
    --slide-size: 85%;

    width: 200px;
  }
  .emblaButton {
    width: 2rem;
  }
}
