.contactContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-bottom: 20px;
}
.contactDiv {
  width: 100%;
  padding: 30px;
  text-align: center;
}
.contactDivDiv {
  padding: 10% 25%;
  border-radius: 50px;
  background-color: #ffaf35;
}
.contactDiv div h1 {
  font-size: 42px;
  margin-bottom: 10px;
}
.contactDiv div p {
  color: #292929;
  margin-bottom: 10px;
}
.contactForm {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

.contactForm div:first-child {
  width: 100%;
}
.contactFormInput input {
  width: 100%;
  border-radius: 10px;
  padding: 15px 10px;
  box-sizing: content-box;
  border: none;
}
@media (max-width: 800px) {
  .contactForm {
    flex-direction: column;
  }
}
@media (max-width: 700px) {
  .contactDiv div h1 {
    font-size: 32px;
  }
  .contactDivDiv {
    padding: 10% 15%;
  }
  .contactContainer {
    padding: 5px;
  }
  .contactDiv {
    padding: 5px;
  }
}
