.emblaButtons {
  display: flex;
  justify-content: end;
  align-items: center;
  position: absolute;
  bottom: -10%;
  right: 25px;
  left: 1.6rem;
}
.emblaButton {
  background-color: #fff;
  border-radius: 50%;
  text-decoration: none;
  cursor: pointer;
  border: 0;
  padding: 0;
  margin: 0;
  z-index: 1;
  color: #127c71;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 2.5rem;
  height: 2.5rem;
  margin-left: 5px;
}
.emblaButton:hover {
  background-color: #127c71;
  color: #fff;
}

.emblaButtonSvg {
  width: 45%;
  height: 45%;
}
.cardButton {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.cardButton div:last-child button {
  padding: 10px 12px;
  border: none;
  color: #fff;
  background-color: #127c71;
  border-radius: 100%;
}
