.mentorContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f2f5f5;
  padding: 50px 0px;
  text-align: center;
}
.embla {
  --slide-spacing: 1rem;
  --slide-size: 27%;
  --slide-height: 12rem;
  padding: 1.6rem;
  width: 80%;
}
.emblaViewport {
  overflow: hidden;
}
.emblaContainer {
  display: flex;
  touch-action: pan-y;
}
.CardContainer {
  flex: 0 0 var(--slide-size);
  min-width: 0;
  padding: var(--slide-spacing);
  margin: var(--slide-spacing);
  background-color: #fff;
  border-radius: 20px;
  text-align: start;
}
.cardHeroImgDiv img {
  border-radius: 20px;
  width: 100%;
}
.cardBottomImgDiv img {
  width: 25%;
  padding: 10px;
}
.emblaButtons {
  display: flex;
  justify-content: end;
  margin-right: 5%;
}

@media (max-width: 1020px) {
  .embla {
    --slide-size: 40%;
    width: 80%;
  }
}
@media (max-width: 600px) {
  .embla {
    --slide-size: 90%;
    width: 70%;
  }
}
@media (max-width: 400px) {
  .embla {
    --slide-size: 85%;
    width: 80%;
  }
}
