.footerContainer {
  background-color: #127c71;
  color: #fff;
  padding: 50px;
  font-size: medium;
}
.footerContainerDiv {
  display: flex;
  justify-content: center;
  gap: 50px;
}
.footerContainerLeft {
  padding: 0px 3px;
}
.footerContainerRight {
  display: flex;
  gap: 50px;
}
.footerContainerRight div {
  display: flex;
  align-items: start;
  justify-content: center;
  flex-direction: column;
  gap: 5px;
}

@media (max-width: 800px) {
  .footerContainerDiv {
    flex-direction: column;
  }
}
@media (max-width: 550px) {
  .footerContainerRight {
    flex-direction: column;
  }
}
